<template>
  <li class="table__row">
    <div class="table__column table__column--name">
      <input
        type="text"
        :value="item.name"
        :class="{ disabled: isReadonly }"
        :readonly="isReadonly"
        @change="onChangeName"
      />
      <div class="table__buttons">
        <img
          v-if="item.children.length"
          @click="show"
          :class="['down_arrow', { opened: isShow }]"
          src="data:image/svg+xml;base64,"
        />
        <img
          @click="onAddItem"
          class="add-button__icon"
          src="data:image/svg+xml;base64,"
        />
      </div>
    </div>
    <div class="table__column table__column--count">
      <input
        type="number"
        :value="isReadonly ? item.totalCount : item.count"
        :class="{ disabled: isReadonly }"
        :readonly="isReadonly"
        @change="onChangeCount"
      />
    </div>
    <div class="table__column table__column--actions">
      <img
        @click="onEditItem"
        class="edit_icon"
        src="data:image/svg+xml;base64,"
      />
      <img
        @click="onDeleteItem"
        class="trash-bin_icon"
        src="data:image/svg+xml;base64,"
      />
    </div>
    <ul
      class="table__row table__row--item-children"
      v-if="isShow && item.children.length"
    >
      <div class="table__rows">
        <row-item
          v-for="(child, index) in item.children"
          :item="child"
          :items="item.children"
          :key="`${child.name}-${index}`"
          @delete-item="onDeleteChild"
          @input-name="onChangeChildName"
          @input-count="onChangeChildCount"
          @add-item-to-child="onAddItemToChild"
          @update-item="onUpdateChild"
        />
      </div>
    </ul>
  </li>
</template>

<script>
export default {
  name: "RowItem",
  props: {
    item: { type: Object, required: true },
    items: { type: Array, required: true }
  },
  data: () => ({
    isShow: false,
    isReadonly: true
  }),
  methods: {
    show() {
      this.isShow = !this.isShow;
    },
    onEditItem() {
      this.$emit("update-item", this.$props.item);
    },
    onUpdateChild(obj) {
      this.$emit("update-item", obj);
    },
    onDeleteItem() {
      this.$emit("delete-item", {
        item: this.$props.item,
        items: this.$props.items
      });
    },
    onDeleteChild(obj) {
      this.$emit("delete-item", obj);
    },
    onChangeName($event) {
      this.$emit("input-name", $event.target.value.trim());
    },
    onChangeCount($event) {
      this.$emit("input-count", $event.target.value);
    },
    onChangeChildName(obj) {
      this.$emit("input-name", obj);
    },
    onChangeChildCount(obj) {
      this.$emit("input-count", obj);
    },
    onAddItemToChild(obj) {
      this.$emit("add-item-to-child", obj);
    },
    onAddItem() {
      this.$emit("add-item-to-child", this.item.children);
    }
  }
};
</script>

