  max-width: 18px;
  max-height: 18px;
  margin-right: 10px;
  cursor: pointer;
  transform: rotate(-90deg);
  transition: transform 0.3s cubic-bezier(0.19, 1, 0.22, 1);
  &.opened {
    transform: rotate(0deg);
  }
}

.table__buttons {
  display: flex;
  flex-direction: row-reverse;
  justify-content: center;
  align-items: center;
  margin-right: 10px;
}

.table__column {
  border-radius: 5px;
  &--name {
    > input {
      margin: 10px 15px;
      font-size: 20px;
      padding: 0 5px;
      border: 2px solid darkcyan {
        radius: 5px;
      }
      &.disabled {
        border-color: transparent;
        outline: unset;
        box-shadow: unset;
      }
    }
    .add-button__icon {
      margin-right: 10px;
      cursor: pointer;
    }
  }
  &--count {
    position: relative;
    > input {
      position: absolute;
      padding: 0 10px;
      font-size: 18px;
      border: 2px solid darkcyan {
        radius: 5px;
      }
      max-width: 100px;
      &.disabled {
        border-color: transparent;
        outline: unset;
        box-shadow: unset;
      }
    }
  }
}

.edit_icon,
.trash-bin_icon {
  max-width: 18px;
  max-height: 18px;
  margin: 10px;
  cursor: pointer;
}

.table__rows {
  margin-left: 20px;
  flex: 1;
  > .table__row {
    > div:first-of-type {
      flex: 5;
    }
  }
}

.table__row {
  &--item {
    &-children {
      flex-direction: row;
      width: 100%;
    }
  }
}
</style>
